<template>
	<div class="fill-page">
		<van-cell-group>
			<van-field label="单位名称" v-model="info.unit" center clearable placeholder="请输入信息" />
			<van-field label="所在部门" v-model="info.department" center clearable placeholder="请输入信息" />
			<van-field label="姓名" v-model="info.realName" center clearable placeholder="请输入信息" />
			<van-field label="性别" center>
				<van-radio-group v-model="info.sex" type="primary" direction="horizontal" slot="input">
					<template v-for="(item, i) in sexs">
						<van-radio :name="item.value">{{item.name}}</van-radio>
					</template>
				</van-radio-group>
			</van-field>
			<van-field label="职务" v-model="info.job" center clearable placeholder="请输入信息" />
			<van-field label="身份证号码" v-model="info.idCard" center clearable placeholder="请输入信息" />
			<van-field label="电子邮箱" v-model="info.email" center clearable placeholder="请输入信息" />
			<van-field label="手机号码" v-model="info.phone" type="tel" maxlength="11" center clearable placeholder="请输入信息" />
		</van-cell-group>
		<div class="page-footer">
			<van-button round block color="#1AAD19" type="primary" @click="submit">提交</van-button>
		</div>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	export default {
		name: 'fill',
		data() {
			return {
				sexs: [{
					name: '男',
					value: 0
				}, {
					name: '女',
					value: 1
				}]
			}
		},
		computed: {
			...mapState(['flag', 'info'])
		},
		methods: {
			// 表单校验方法
			verify() {
				if(!this.info.phone) {
					return '请输入手机号'
				} else if(this.info.phone.length !== 11) {
					return '请输入11位手机号'
				} else {
					return false
				}
			},
			// 提交
			async submit() {
				if(this.verify()) {
					this.$toast(this.verify())
				} else {
					let params = Object.assign({ type: 1 }, this.info)
					let res = await this.$Request.post('/student/addOrEdit.do', params)
					if(res && res.rCode === 0) {
						this.isedit = false
						this.$toast.success('提交成功！')
						this.$Utils.setItem("flag", this.flag, 1)
						this.$Utils.setItem("info", this.info, 1)
						this.setFlag(this.flag)
						this.setInfo(this.info)
					}
				}
			},
			...mapMutations(['setFlag', 'setInfo'])
		}
	}
</script>

<style>

</style>


